.sidebar {
  background-color: #1e213a;
  flex-basis: 25%;
  border-right: 1px solid rgba(255, 255, 255, 0.055);
  padding: 1rem;
  max-width: 500px;
  overflow: hidden;
  position: relative;

  @media(max-width: 830px) {
    max-width: 100%;
    min-height: 100%;
  }

  .sidebar-actions {
    button {
      color: whitesmoke;
    }
  }

  .sidebar-weather {
    > * {
      z-index: 1;
    }
    display: flex;
    flex-direction: column;
    gap: 60px;
    margin-block-start: 5rem;
    align-items: center;

    .degree {
      font-size: 4rem;

      .subtle {
        font-size: 1.5rem;
      }
    }

    .status {
      font-size: 2rem;
    }

    .time-loc {
      gap: 15px;
      opacity: 0.8;
      .loc {
        text-transform: capitalize;
        width: 100%;
        gap: 5px;
      }
    }

    .feels-like {
      gap: 5px;
      margin-block-start: 8px;
      & .feels-like-temp {
        font-size: 1.75rem;
      }

      .subtle {
        font-size: 1rem;
      }
    }
  }
}
