.weather-details {
    background-color: #100e1d;
    flex-basis: 75%;

    .next-days {
        font-size: 1.3rem;
        gap: 2rem;
        justify-content: center;
        font-family: 'Lato', sans-serif;
        flex-wrap: wrap;
        .day-card {
            padding: 1rem;
            gap: 20px;
            background-color: #1c1f39;
            border-radius: 3px;
            min-width: 150px;
            .date {
                text-transform: capitalize;
            }

            .temp {
                font-size: 1rem;
            }

        }
    }
}

.degree-switch {
    padding: 2rem;
    justify-content: flex-end;
    gap: 5px;
    
    button {
        height: 35px;
        width: 35px;
        justify-content: center;
        background-color: #2f3144;
        color: whitesmoke;
        &:hover {
            background-color: none;
        }

        &.active {
            background-color: whitesmoke;
            color: #2f3144;
          }
    }
}

.today-highlights {
    margin-block-start: 4rem;
    display: grid;
    grid-template-rows: max-content 1fr;
    place-content: center;
    gap: 1rem;
    header {
        font-family: 'Lato', sans-serif;
        text-transform: capitalize;
        font-size: 1.6rem;
        place-self: center;
    }
    .highlight-grid {
        display: grid;
        align-self: center;
        grid-template-columns: repeat(2, minmax(200px, 300px));
        gap: 25px;
        margin-block-start: 1rem;

        @media(max-width: 450px) {
            grid-template-columns: 1fr;
        }

        .card {
            background-color: #1e213a;
            padding: 1rem;
            font-family: 'Actor', 'Lato', sans-serif;

            .card-title {
                text-transform: capitalize;
                display: flex;
                align-items: center;
                gap: 7px;
                font-family: 'Lato', sans-serif;
                img {
                    max-width: 25px;
                    filter: invert(99%) sepia(3%) saturate(608%) hue-rotate(346deg)
                    brightness(119%) contrast(100%);
                
                }
            }

        }
    }
}