// Externals
@import url('https://fonts.googleapis.com/css2?family=Cookie&family=Lato&family=Lexend+Deca:wght@300&family=Nanum+Gothic:wght@400;700&family=Noto+Sans+KR:wght@700&family=Roboto&family=Satisfy&family=Staatliches&family=Sue+Ellen+Francisco&family=Thasadith:wght@700&family=Varela+Round&display=swap&family=Actor&display=swap');
// Basics
@import 'basics/typography';
@import 'basics/base';
@import 'basics/helpers';
@import 'basics/layout';
@import 'basics/animations';

// Cmps
@import 'cmps/sidebar';
@import 'cmps/weather-details';
@import 'cmps/city-search';
@import 'cmps/loader';

// Reusable

@import 'reusable/button';

.sunny-svg {
    filter: invert(67%) sepia(10%) saturate(2274%) hue-rotate(338deg)
      brightness(108%) contrast(139%);
  }
  
  .rain-svg {
    filter: invert(79%) sepia(11%) saturate(1072%) hue-rotate(177deg)
      brightness(104%) contrast(103%);
  }
  
  .loc-svg {
      filter: invert(99%) sepia(3%) saturate(608%) hue-rotate(346deg)
      brightness(119%) contrast(100%);  
  
  }
  
  .cloud-svg {
      filter: invert(74%) sepia(5%) saturate(246%) hue-rotate(153deg) brightness(96%) contrast(95%);
  }
  
  .snow-svg {
    filter: invert(74%) sepia(15%) saturate(929%) hue-rotate(183deg) brightness(103%) contrast(102%);
  }

  .search-icon {
    max-width: 20px;
    filter: invert(99%) sepia(3%) saturate(608%) hue-rotate(346deg)
    brightness(119%) contrast(100%);
    position: absolute;
    top: 6px;     
    right: 6px;

  }