.city-search {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    background-color: #1c1f39;
    transform: translateX(-100%);
    transition: transform ease .2s;
    padding: 1rem;
    &.visible {
        transform: translateX(0);
    }

    label {
        display: flex;
        gap: 8px;
        margin-top: 1rem;
        position: relative;
        input {
            width: 100%;
        }

    }
}

.autocomplete-list {
    gap: 5px;
    margin-top: 1rem;
    
    li {
        padding: 0.5rem;
        cursor: pointer;

        &:hover {
            outline: 1px solid rgba(245, 245, 245, 0.249);
        }

    }
}